import { Directive, ViewContainerRef, ViewRef, Input } from '@angular/core';

/**
 * @hidden
 */
@Directive({
  selector: '[templateContext]' // tslint:disable-line:directive-selector-prefix
})
export class TemplateContextDirective {
    private insertedViewRef: ViewRef;

    constructor(private viewContainerRef: ViewContainerRef) {}

    @Input() set templateContext(context: any) {
        if (this.insertedViewRef) {
            this.viewContainerRef.remove(this.viewContainerRef.indexOf(this.insertedViewRef));
            this.insertedViewRef = undefined;
        }

        if (context.templateRef) {
            this.insertedViewRef = this.viewContainerRef.createEmbeddedView(context.templateRef, context);
        }
    }
}



